.App{
  width: 100vw;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;

}

.Sidebar {
  height: 100%;
  width: 250px;
  background-color: #3546C8;
}

.Menu{
  width: 100%;
  height: auto;
  margin: auto;
  color:#ACACAC;
  text-align-last: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.SidebarList{
  height: auto;
  padding: 0;
  width: 100%;
}

.SidebarList .row{
  width: 100%;
  height: 70px;
  list-style-type: none;
  margin: 0;
  display: flex;
  flex-direction: row;
  color:#ACACAC;
  justify-content: center;
  align-items: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.SidebarList .row:hover{
  cursor: pointer;
  background-color: #1F256E;
  color: white;
}

.row #icon {
  flex: 20%;
  display: grid;
  place-items: center;

}
.row #title {
  flex: 70%;
  
}
.row #dropdown {
  flex: 20%;
  
}